.set-up{
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
  padding-top: 25px;
  background-color: #fff;
  
  .set-up-navbar{
    border-bottom: 1px solid #d8d8d8;
  }
  .set-up-box{
    width: 100%;
    height: calc(100vh - 60px);
    padding-bottom: 5vw;
    box-sizing: border-box;
    background-color: #f4f4f4;
    overflow: auto;
    .cell-box{
      .za-cell__header .za-cell__icon i{
        width: 6vw;
        height: 6vw;
        border-radius: 50%;
        background-color: #9c9c9c;
        position: relative;
        display: block;
        svg.icon{
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          width: 4vw;
          height: 4vw;
          vertical-align: middle;
          fill: currentColor;
          overflow: hidden;
          color: #fff;
        }
      }
      .za-cell__footer span.important{
        background-color: #e3271e;
        padding: 1.5vw 2vw;
        border-radius: 5vw;
        color: #fff;
        font-size: 3vw;
      }
    }
    .sign-out-but{
      width: 100%;
      padding: 4vw;
      box-sizing: border-box;
      font-size: 3.5vw;
      background-color: #fff;
      text-align: center;
    }
  }
}